.balance {
  background: rgba(82, 59, 126, 0.6);
  box-shadow: 0px 4px 60px 0px rgba(0, 0, 0, 0.25);
  padding: 10px 15px 10px 25px;
  width: 100%;
  height: 80px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  gap: 11px;
  align-self: center;

  overflow: hidden;
  white-space: nowrap;
}

.balance h3 {
  font-size: 12px;
  font-weight: 400;
  line-height: 13px;
  color: rgba(255, 255, 255, 0.4);
  text-transform: uppercase;
}

.balance p {
  color: #fbfbfb;
  font-size: 30px;
  font-weight: 700;
  line-height: 36px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  transition: all 0.5s ease 0s;
  cursor: help;
}

.balance:hover > p {
  text-overflow: clip;
  white-space: normal;
  word-break: break-all;
  transition: all 0.5s ease;
}

@media screen and (min-width: 768px) {
  .balance {
    width: 336px;
    align-self: start;
  }
}

@media screen and (min-width: 1280px) {
  .balance {
    width: 480px;
  }
}
