.modalFooter {
  position: fixed;
  top: 60px;
  left: 0;

  height: calc(100vh - 60px);
  width: 100%;

  z-index: 150;

  background: linear-gradient(
    0deg,
    rgba(83, 61, 186, 1) 0%,
    rgba(80, 48, 154, 1) 43.14%,
    rgba(106, 70, 165, 1) 73.27%,
    rgba(133, 93, 175, 1) 120.03%
  );

  box-shadow: 0px 4px 60px 0px #00000040;
  backdrop-filter: blur(50px);

  overflow: auto;
}

.modalFooter::-webkit-scrollbar {
  display: none;
}

.modalContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 40px;
  padding: 20px;
  min-height: 100%;
}

.modalContent h2 {
  color: #ffffff;
  font-size: 20px;
  font-weight: bold;
  line-height: 27px;
  margin-bottom: -15px;
}

.thanksBtn {
  margin-top: -15px;
  animation-delay: 6.5s;
}

@media screen and (min-width: 768px) {
  .modalFooter {
    top: 0;
    min-height: 100vh;
    background: #220d5b3b;
    backdrop-filter: blur(3px);
    display: flex;
    justify-content: center;
    padding: 40px 118px;
  }

  .modalContent {
    background: rgba(82, 59, 126, 1);
    box-shadow: 0px 4px 60px 0px #00000040;
    border-radius: 8px;
    min-height: fit-content;
    height: fit-content;
    width: fit-content;
    padding: 35px 70px;
    margin: auto 0;
  }
}

.teamList {
  list-style-type: none; /* Elimină bulinele */
  padding: 0; /* Elimină spațiul de la începutul listei */
}

.teamList li {
  margin-bottom: 10px; /* Spațiu între elemente */
}

.teamList a {
  color: white; /* Schimbă culoarea textului în alb */
  text-decoration: none; /* Elimină sublinierea */
}

.teamList a:hover {
  text-decoration: underline; /* Adaugă sublinierea la hover, dacă dorești acest efect */
}
