.footer {
  background: linear-gradient(270.02deg, #2e1746 3.2%, #2e225f 99.98%);
  padding-top: 12px;
  padding-bottom: 12px;
  box-shadow: 0px 4px 40px 0px rgba(0, 0, 0, 0.25),
    0px -3px 2px 0px rgba(0, 0, 0, 0.1) inset;
  color: rgba(255, 255, 255, 0.6);
  text-align: center;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.5px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer .icon {
  margin-left: 10px;
  margin-right: 10px;
}

.footer .students {
  margin-left: 7px;
  text-decoration: underline;
  cursor: pointer;
  transition: all 0.5s ease 0s;
  color: #fbfbfb;
}

.footer .students:hover {
  color: #ff868d;
  transition: all 0.5s ease 0s;
  font-size: 18px;
}

/* TABLET */
@media screen and (min-width: 768px) {
  .footer {
    height: 65px;
  }
}
